import styled from 'styled-components';

const StyledButton = styled.div`
  width: 100%;
  padding: 12px;
  border-radius: $m-border-radius / 2;
  font-size: $f-xs;
  font-weight: bold;

  &--submit,
  &--primary {
    background: $c-blue;
    color: $c-white;
  }

  &:disabled {
    cursor: not-allowed;
  }

  &--disabled {
    color: $c-disabled-txt;
    background: $c-disabled-bg;
  }
`;

const Button = props => {
  const { as, handleClick, href } = this.props;

  if (as === 'link') {
    return (
      <Link href={href}>
        <StyledLink>
          {props.children}
        </StyledLink>
      </Link>
    ); 
  }

  return (
    <StyledButton onClick={handleClick}>
      {props.children}
    </StyledButton>
  );
}

export default Button;